.welcome {
    position: absolute;    
    color: rgb(63, 63, 63);
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.welcome>h1 {
    margin: 0 auto 20px auto;
    text-align: center;
    width: 90%;
}

.welcome>p {
    margin: 0 auto;
    width: 80%;
    text-align: center;
}

@media (min-width:1068px) {
    .welcome>p {
        width: 100%;
        box-sizing: border-box;
        padding: 0px 25%;
    }
}